import React, { useState } from 'react';
import { MobileStepper, Paper, Typography, Button } from '@mui/material';
import CircleIcon from '@mui/icons-material/Circle';

const images = [
  {
    label: 'Bridge',
    url: 'https://images.unsplash.com/photo-1537944434965-cf4679d1a598?auto=format&fit=crop&w=400&h=250&q=60',
  },
  {
    label: 'Birds',
    url: 'https://images.unsplash.com/photo-1538032746644-0212e812a9e7?auto=format&fit=crop&w=400&h=250&q=60',
  },
  {
    label: 'House',
    url: 'https://images.unsplash.com/photo-1537996194471-e657df975ab4?auto=format&fit=crop&w=400&h=250',
  },
  {
    label: 'Trees',
    url: 'https://images.unsplash.com/photo-1512341689857-198e7e2f3ca8?auto=format&fit=crop&w=400&h=250&q=60',
  },
];

function CarouselExample1() {
  let [imageIndex, setImageIndex] = useState(0);

  const goPreviousImage = () => {
    let newImageIndex = imageIndex - 1;
    if (newImageIndex < 0) {
      newImageIndex = images.length - 1;
    }
    setImageIndex(newImageIndex);
  };
  const goNextImage = () => {
    let newImageIndex = imageIndex + 1;
    if (newImageIndex === images.length) {
      newImageIndex = 0;
    }
    setImageIndex(newImageIndex);
  };

  return (
    <div>
      {/* imageIndex {imageIndex} {images[imageIndex].label} */}
      <div>
        <div style={{ textAlign: 'center' }}>
          {/* Image */}
          <img src={images[imageIndex].url} />
          <br />

          {/* Image label */}
          <div style={{ textAlign: 'left' }}>{images[imageIndex].label}</div>
          <br />

          {/* Back button */}
          <Button style={{ fontSize: '25px' }} onClick={goPreviousImage}>
            {'<'}{' '}
          </Button>

          {/* Circles */}
          {images.map((item, index) => {
            return (
              <>
                {index === imageIndex ? ( //
                  <CircleIcon style={{ fontSize: '10', color: 'blue' }} />
                ) : (
                  <CircleIcon style={{ fontSize: '10', color: 'grey' }} />
                )}
              </>
            );
          })}

          {/* Next button */}
          <Button style={{ fontSize: '25px' }} onClick={goNextImage}>
            {'>'}
          </Button>
        </div>
      </div>
    </div>
  );
}

function CarouselExample2() {
  const [imageIndex, setImageIndex] = useState(0);

  const handleNext = () => {
    setImageIndex((previousImageIndex) =>
      previousImageIndex === images.length - 1 ? 0 : previousImageIndex + 1
    );
  };

  const handleBack = () => {
    setImageIndex((previousImageIndex) =>
      previousImageIndex === 0 ? images.length - 1 : previousImageIndex - 1
    );
  };

  return (
    <div>
      <Paper style={{ maxWidth: 400, flexGrow: 1 }}>
        <img
          style={{ width: '100%', height: 'auto' }}
          src={images[imageIndex].url}
          alt={images[imageIndex].label}
        />
        <Typography>{images[imageIndex].label}</Typography>
      </Paper>
      <MobileStepper
        images={images.length}
        position="static"
        imageIndex={imageIndex}
        nextButton={<Button onClick={handleNext}>Next</Button>}
        backButton={<Button onClick={handleBack}>Back</Button>}
      />
    </div>
  );
}

function CarouselExample() {
  return (
    <div>
      <div>
        <CarouselExample1 />
      </div>
      <div>
        <CarouselExample2 />
      </div>
    </div>
  );
}

export default CarouselExample;
